.Card {
    background-color: red;
    border: 3px solid red;
    border-radius: 10px;
    overflow: hidden;
    font-family: 'Oswald', sans-serif;
    margin: 15px;



}

.Card .Title {
    padding: 10px 0px;
    display: flex;
    justify-content: center;
}

.Card .Content{
    background-color: #ffffff;
    color: black;
    padding: 20px;
    display: flex;
    justify-content: center;


}

h1, h2, h3 {

    font-weight: 200;
}

 h2, h3 {

   margin: 0;
}